:root {
  --X-color: #31c3bd;
  --X-lighter-color: #65e9e4;
  --Y-color: #f2b137;
  --Y-lighter-color: #ffc860;
  --bgc-color: #1a2a33;
  --element-color: #1f3641;
  --text-color: #dbe8ed;
  --darker-text-color: #a8bfc9;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background-color: var(--bgc-color);
  font-family: Outfit, sans-serif;
  overflow-y: hidden;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.hidde {
  display: none !important;
}

.new-game, .game-board-section {
  flex-direction: column;
  align-items: center;
  max-width: 46rem;
  margin: 10rem auto;
  animation: 1s ease-in-out fadeIn;
  display: flex;
}

.logo {
  padding-bottom: 3rem;
}

.choose-player-container {
  background-color: var(--element-color);
  -webkit-box-shadow: 0 10px #1a2127;
  -moz-box-shadow: 0 10px #1a2127;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
  padding: 2.4rem;
  display: flex;
  box-shadow: 0 8px #1a2127;
}

.choose-headband {
  color: var(--text-color);
  letter-spacing: 1.25px;
  font-size: 1.8rem;
}

.player-choose {
  background-color: var(--bgc-color);
  border-radius: 1rem;
  justify-content: space-between;
  align-items: center;
  min-width: 100%;
  height: 7rem;
  margin: 2.4rem 0 1.7rem;
  padding: .9rem .8rem;
  display: flex;
}

.game-start__x-mark, .game-start__o-mark {
  border-radius: inherit;
  flex-basis: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: all .4s;
  display: flex;
  position: relative;
}

.game-start__x-mark:hover, .game-start__o-mark:hover {
  background-color: var(--element-color);
  cursor: pointer;
}

.game-start__x-mark.selected, .game-start__o-mark.selected {
  background-color: var(--darker-text-color);
}

.game-start__x-mark.selected > svg, .game-start__o-mark.selected > svg {
  fill: #1a2a33;
}

.game-start__x-icon, .game-start__o-icon {
  fill: var(--darker-text-color);
  width: 3.2rem;
  height: 3.2rem;
}

.x-plater, .o-plater {
  height: 7rem;
}

.choose-player path {
  fill: var(--darker-text-color);
  width: 2px;
  height: 2px;
}

.choos-foote-text {
  color: #d9d9d9;
  letter-spacing: 1px;
  font-size: 1.4rem;
}

.vs-cpu-btn {
  letter-spacing: 1.25px;
  background-color: var(--Y-color);
  -webkit-box-shadow: 0 10px #c29338;
  -moz-box-shadow: 0 10px #c29338;
  margin-top: 3.8rem;
  box-shadow: 0 8px #c29338;
}

.vs-cpu-btn:hover {
  cursor: pointer;
  background-color: var(--Y-lighter-color);
  transition: all .3s;
}

.vs-player-btn {
  letter-spacing: 1.25px;
  background-color: var(--X-color);
  -webkit-box-shadow: 0 10px #318682;
  -moz-box-shadow: 0 10px #318682;
  margin-top: 2.6rem;
  box-shadow: 0 8px #318682;
}

.vs-player-btn:hover {
  cursor: pointer;
  background-color: var(--X-lighter-color);
  transition: all .3s;
}

.vs-cpu-btn, .vs-player-btn {
  color: var(--bgc-color);
  border: none;
  border-radius: 14px;
  min-width: 100%;
  padding: 1.2rem 0;
  font-family: inherit;
  font-size: 1.8rem;
  font-weight: 650;
}

.game-board-section {
  gap: 2rem;
}

.game-board-header {
  justify-content: space-around;
  align-items: center;
  gap: 8rem;
  min-width: 100%;
  display: flex;
}

.gameplay__turn {
  background-color: #1f3641;
  border-radius: 1rem;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  min-width: 14rem;
  margin-left: -2rem;
  padding: 2rem 1rem;
  display: flex;
  box-shadow: inset 0 -.4rem #0f191e;
}

.gamplay__turn-icon {
  fill: #a8bfc9;
  width: 2.2rem;
  height: 2.2rem;
}

.gamplay__turn__text {
  color: var(--darker-text-color);
  font-size: 1.8rem;
  font-weight: 600;
}

.reset-game {
  background-color: var(--darker-text-color);
  -webkit-box-shadow: 0 10px #7b9294;
  -moz-box-shadow: 0 10px #7b9294;
  border: none;
  border-radius: 9px;
  padding: 1rem 1.4rem;
  box-shadow: 0 4px #7b9294;
}

.reset-game:hover {
  cursor: pointer;
  background-color: var(--text-color);
  transition: all .3s;
}

.game-board {
  grid-template-columns: repeat(3, 1fr);
  gap: 3rem 2rem;
  min-width: 100%;
  display: grid;
}

.board-box {
  background-color: var(--element-color);
  -webkit-box-shadow: 0 10px #1a2127;
  -moz-box-shadow: 0 10px #1a2127;
  border-radius: 19px;
  width: 14rem;
  height: 14rem;
  box-shadow: 0 8px #1a2127;
}

.board-X:hover {
  cursor: pointer;
  background-image: url("icon-x-outline.c3c82311.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.board-O:hover {
  cursor: pointer;
  background-image: url("icon-o-outline.513aa494.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.selected-by-X {
  background-image: url("icon-x.4cde2696.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.board-box.selected-by-X:hover {
  cursor: default;
  background-image: url("icon-x.4cde2696.svg");
}

.selected-by-O {
  background-image: url("icon-o.b2d6f941.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.board-box.selected-by-O:hover {
  cursor: default;
  background-image: url("icon-o.b2d6f941.svg");
}

.game-board-footer {
  gap: 2rem;
  display: flex;
}

.X-point-counter, .ties-counter, .O-point-counter {
  color: var(--bgc-color);
  letter-spacing: 1px;
  border-radius: 16px;
  flex-direction: column;
  align-items: center;
  min-width: 14rem;
  margin-top: 1rem;
  padding: 1.4rem 0;
  font-size: 1.6rem;
  display: flex;
}

.X-point-counter {
  background-color: var(--X-color);
}

.ties-counter {
  background-color: var(--darker-text-color);
}

.O-point-counter {
  background-color: var(--Y-color);
}

.counter {
  color: var(--bgc-color);
  letter-spacing: 1.25px;
  font-size: 2rem;
  font-weight: 700;
}

.overlay {
  z-index: 5;
  background-color: #000000a6;
  width: 100%;
  height: 150vh;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-winner-window {
  z-index: 10;
  background-color: var(--element-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  padding: 4rem 0;
  animation: .4s ease-in-out fadeIn;
  display: flex;
  position: absolute;
  top: 30%;
}

.modal__winner-text {
  letter-spacing: 1.25px;
  color: var(--text-color);
  margin-top: 1rem;
  font-size: 2rem;
  font-weight: 550;
}

.modal__header {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.modal__winner__icon {
  width: 8.6rem;
  height: 8.6rem;
}

.modal__header__text {
  letter-spacing: 2.5px;
  font-size: 4rem;
  font-weight: 700;
}

.X__win__text {
  color: var(--X-color);
}

.O__win__text {
  color: var(--Y-color);
}

.modal__buttons {
  gap: 1rem;
  margin-top: 1rem;
  display: flex;
}

.btn__left, .btn__right {
  cursor: pointer;
  color: var(--bgc-color);
  letter-spacing: 1px;
  border: none;
  border-radius: 11px;
  outline: none;
  padding: 1.2rem 1.6rem;
  font-size: 2rem;
  font-weight: 550;
}

.btn__left {
  background-color: var(--darker-text-color);
  -webkit-box-shadow: 0 10px #7b9294;
  -moz-box-shadow: 0 10px #7b9294;
  box-shadow: 0 4px #7b9294;
}

.btn__left:hover {
  background-color: var(--text-color);
  transition: all .3s;
}

.btn__right {
  background-color: var(--Y-color);
  -webkit-box-shadow: 0 6px #c29338;
  -moz-box-shadow: 0 10px #c29338;
  box-shadow: 0 4px #c29338;
}

.btn__right:hover {
  background-color: var(--Y-lighter-color);
  transition: all .3s;
}

.restart__text {
  color: var(--darker-text-color);
  margin-bottom: 2rem;
}

.block-event {
  pointer-events: none;
}

.win-X {
  background-color: var(--X-color);
  padding: 3.8rem;
  cursor: not-allowed !important;
}

.win-X:before {
  content: url("icon-x-win-board.77c40900.svg");
}

.win-O {
  background-color: var(--Y-color);
  padding: 3.8rem;
  cursor: not-allowed !important;
}

.win-O:before {
  content: url("icon-o-win-board.17d9434f.svg");
}

